.product--card {
  width: 15rem;
  height: min-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  border: 2px solid whitesmoke;
  border-radius: 10px;
}

.product--card:hover {
  border: 2px solid whitesmoke;
  border-radius: 10px;
  cursor: pointer;
}

.product--card:hover .product--card__img {
  transform: scale(1.1);
  transition-duration: 300ms;
  overflow: hidden;
}

.product--card__img {
  width: 10rem;
  height: 10rem;
}

.product--card__img img {
  height: 100%;
  width: 100%;
}

.products--card__footer {
  display: flex;
  justify-content: space-between;
}
