.filter_wrapper {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.size__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.size__wrapper > span {
  padding: 0.3rem;
  min-width: 1rem;
  border: 1px solid black;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
}

.filter__item {
  display: flex;
  gap: 5rem;
}

.filter__content {
  display: flex;
}
